@media (max-width: 767px) {
    .header_burger {
        position: relative;
        display: block;
        width: 47px;
        height: 28px;
        border-top: 0px solid #fff;
        border-bottom: 2px solid #fff;
        left :425%;
        top: -53px;
    }

    .header_menu.active {
        top: 0;
        display: block;
    }

    .header_burger:before,
    .header_burger:after {
        content: '';
        background-color: white;
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        z-index: 2;
        top: 15px
    }

    .header_burger:before {
        bottom: 0px;
    }

    .header_burger:after {
        top: 0px;
    }

    .header_burger.active:before {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        transition: all 0.3s ease 0s;
        top: 11px;
    }

    .header_burger.active:after {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        transition: all 0.3s ease 0s;
        top: 10px
    }

    .header_burger.active span {
        -webkit-transform: scale(0);
                transform: scale(0);
        transition: all 0.3s ease 0s;
    }

    .header_burger span {
        position: absolute;
        background-color: white;
        left: 0;
        width: 100%;
        height: 2px;
        top: 30px;
        z-index: 2;
    }

    .header_menu {
        position: fixed;
        display: none;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:#90E6E5;
        padding: 70px 0px 0px 0px;
        transition: all 0.3s ease 0s;
    }

    .header_list {
        display: block;
        list-style: none;
        margin-top: 30px;
    }

    .header_link {
        line-height: 4;
        text-decoration: none;
        color: black;
        text-transform: uppercase;
        transition-property: color;
        transition-duration: 500ms;
    }
}


   
















